import Vue from 'vue';
import axios from 'axios';
import store from './store'
// Vue.use(axios);  axios不支持use安装


  
      
    // 推荐一个请求的拦截器
    axios.interceptors.request.use(function (config) {
      // 做点事 config = {header,params,data,method,}
    store.commit('VIEW_LOADING',true)  //直接交个mutations acitons无业务逻辑可做
      return config;
    }, function (error) {
      // Do something with request error
      return Promise.reject(error);
    });
  
    // 添加一个响应的拦截器
    axios.interceptors.response.use(function (response) {
      // 做点事
    store.commit('VIEW_LOADING',false)
      return response;
    }, function (error) {
      // Do something with response error
      return Promise.reject(error);
    });
  
  
  

Vue.prototype.$axios = axios;// 组件里面: this.$axios;
window.axios = axios;        //组件，非组件里面都可以  axios()

export default axios;
